body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 14px;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

/*general*/
#root {
  position: relative;
}

.hide {
  display: none;
}

.mr-9 {
  margin-right: 9%;
}

.navbar {
  position: fixed;
  width: 100%;
  padding-top: 9px;
  border-radius: 0;
  margin-bottom: 0;
  z-index: 5;
}

.form-control {
  height: 30px;
}

.none {
  display: none;
}

.sidebar {
  position: fixed;
  top: 50px;
  bottom: 0;
  left: 0;
  z-index: 2;
  display: block;
  padding: 20px;
  overflow-x: hidden;
  overflow-y: auto;
  background-color: #f5f5f5;
  border-right: 2px solid #eee;
}


.cardModel-layout {
  position: fixed;
  top: 0; right: 0; bottom: 0; left: 0;
  background-color: rgba(0, 0, 0, .5);
  z-index: 5;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding-bottom: 10%;
}

.cardModel {
  border-radius: 4px;
  padding: 0 15px;
  width: 30%;
  margin: 0 auto;
  background-color: #eee;
  position: relative;
}

.cardModel h3 {
  text-align: center;
  font-weight: 600;
}

.cardModel label {
  display: block;
}

.cardModel p {
  display: block;
  margin-top: 12px;
}

.cardModel p button:first-of-type {
  margin-right: 10px;
}

.cardModel textarea {
  border-radius: 4px;
  border: none;
  border-bottom: 2px solid #ccc;
  width: 100%;
  min-height: 64px;
}

.main {
  margin-top: 50px;
  padding: 30px 40px;
}

.card {
  position: relative;
  text-align: center;
  background-color: #f5f5f5;
  padding: 10px;
  border-radius: 2px;
  border: 1px solid #eee;
  margin-bottom: 30px;
  height: 64px;
  overflow: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.card:hover .btn {
  display: block;  
}

.card .btn {
  position: absolute;
  bottom: 4px;
  right: 4px;
  display: none;
}

.no-cards {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;

}

.close {
  position: absolute;
  right: 7px;
  top: 1px;
}

.front, .back {
  text-align: center;
}

.flip {
  margin-bottom: 10px;
}

.asc {
  font-weight: 600;
  margin-bottom: 30px;
}

.back button {
  margin-right: 10px;
}

.back button:last-child {
  margin-right: 0;
}

.back-btn {
  margin-bottom: 16px;
}

.nav-sidebar .active {
  color: #000;
  background-color: #ddd;
}

.nav-sidebar .active:focus {
  background-color: #ddd;
}


